import React from "react"
import { StaticQuery, graphql } from 'gatsby';

const TitleAndDescription = ({ data }) => {
  const { title, description } = data.site.siteMetadata;
  return (
    <div className="header">
      <h2>{title}</h2>
      <p>{description}</p>
    </div>
  )
}

 export default () => {
  return (
    <StaticQuery
      query={graphql`
        query {
          site {
            siteMetadata {
              title
              description
            }
          }
        }
      `}
      render={data => <TitleAndDescription data={data} />}
    >
    </StaticQuery>
  )
}
